/* src/components/Fotos/Fotos.css */

.foto-container {
    padding: 20px;
    margin-bottom: 20px;
}

.foto-container h1 {
    color: rgb(0, 107, 204); /* Use taskbar color */
    font-size: 2.5rem; /* Keep it bold and large */
    text-align: center; /* Center the heading */
    font-family: 'Arial', sans-serif; /* Clean and simple font */
    margin-top: 20px; /* Add a bit of space above */
    margin-bottom: 20px; /* Add space below */
    font-weight: 600; /* Slightly bold */
}

.foto-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 items per row by default */
    gap: 20px; /* Space between grid items */
}

.foto-gallery-item {
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.foto-gallery-item:hover {
    transform: scale(1.05);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4);
}

.foto-img {
    width: 100%;
    height: auto;
    display: block;
    border-radius: 12px;
}

/* Responsive adjustments */
@media (max-width: 991.98px) {
    .foto-grid {
        grid-template-columns: repeat(2, 1fr); /* 2 items per row */
    }
}

@media (max-width: 575.98px) {
    .foto-grid {
        grid-template-columns: 1fr; /* 1 item per row */
    }
}

.pagination-buttons {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .pagination-buttons button {
    background-color: rgb(0, 107, 204);
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 0 10px;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .pagination-buttons button:disabled {
    background-color: grey;
    cursor: not-allowed;
  }
  
  .pagination-buttons button:hover:not(:disabled) {
    background-color: rgb(0, 90, 180);
  }
  
